export default{
	data() {
		return {
			baseUrl: getApp().globalData.baseUrl
		}
	},
	props:{
		videoList: Array,
		isNoMore: Boolean
	},
	methods:{
		toVideoPage(index) {
			getApp().globalData.currentPage = index
			if (getApp().globalData.isSearch) {
				// 如果是查询状态，则关闭其他页面
				uni.reLaunch({
					url:'../../pages/video/video',
				})
			} else {
				uni.navigateTo({
					url:'../../pages/video/video',
				})
			}
		}
	}
}
